<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process193"></a>

<p><br></p>
<p><span class="rvts17">高价猎杀（</span><span class="rvts50">A Prize Kill</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:496,Y:166</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：一只鹿的灵魂（</span><span class="rvts20">Spirit of Deer</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助鹿的灵魂寻找它最爱的野花</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1226.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在瑞克的洋馆里的楼梯出口附近，有一只鹿头，使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能与它交谈，它会告诉你它最喜欢那片漫山遍野的野花。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1227.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在石墓花园外围不远处（位置</span><span class="rvts20">X:542,Y:225</span><span class="rvts21">）的一处荒野内，可以找到麋鹿的尸体，并且旁边还有射杀麋鹿的猎人的尸体。</span>
<br><span class="rvts21">使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能会知道这个猎人也是无意杀掉了这只瑞克最爱的麋鹿（在尸体上有瑞克的复仇信），回洋馆与麋鹿对话后，该任务将关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



